import SimpleLightbox from "simplelightbox";
import "simplelightbox/dist/simple-lightbox.min.css";

const list = document.querySelector(".gallery"); 
const item = document.querySelectorAll(".gallery-item");
const img = document.querySelectorAll(".gallery-image");
list.insertAdjacentHTML("afterbegin", createMurkup(images));



function createMurkup(array) {
    return array.map(({ preview, original, description }) => `
  <li class="gallery-item">
  <a class="gallery-link" href="${original}" onclick = "event.preventDefault()">
    <img
      class="gallery-image"
      src="${preview}"
      alt="${description}"
    />
  </a>
</li>`).join(""); 
}
let gallery = new SimpleLightbox('.gallery a', {
    
	captions: true,
	captionType: 'attr',
	captionsData: 'alt',
	captionPosition: 'bottom',
	captionDelay: 250
}); 